.language__wrapper {
  background: $black;
  display: flex;
  justify-content: flex-end;
  .language {
    font-size: 1.4rem;
    padding: .5rem 5rem;
    &--active {
      color: $yellow !important;
    }
    a {
      color: $white;
      text-decoration: none;
    }
    span {
      color: lighten($black, 70%);
    }
  }
}

.logo {
  background: $black;
  display: flex;
  justify-content: center;
}

header {
  background: $black;
  display: grid;
}

.nav {
  z-index: 1;
  // background: $yellow;
  background: $black;
  display: grid;
  grid-template-columns: 1fr;
  grid-auto-flow: column;
  grid-gap: 1rem;

  &__item {
    list-style: none;
  }
  &__item:hover > &__dropdown {
    display: block;
  }
  &__link {
    height: 5rem;
    text-decoration: none;
    text-transform: uppercase;
    color: $white;
    font-size: 1.6rem;
    font-weight: 500;
    position: relative;
    display: flex;
    justify-content: center;
    align-items: center;
    &--active {
      color: $yellow;
    }
  }
  &__link:hover {
    // color: $black;
    color: $yellow;
    transition: all .5s ease;
  }
  &__dropdown {
    display: none;
    position: absolute;
    // background: $white;
    background: $black;
    margin: 0;
    list-style: none;
    padding-left: 0;
    &__link {
      text-decoration: none;
      text-transform: capitalize;
      width: 180px;
      height: 3rem;
      display: grid;
      align-items: center;
      border-bottom: 1px solid rgba(0, 0, 0, .1);
      // color: $yellow;
      color: $white;
      font-size: 1.4rem;
      span {
        padding-left: 2rem;
      }
    }
    &__link:hover {
      color: $yellow;
    }
  }
  .down {
    padding-left: 1rem;
    .fa-angle-up {
      display: none;
    }
  }
}

.nav__section {
  &--pages {
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(100px, 1fr));
    grid-gap: 2rem;
    width: 60%;
    margin: 0 auto;
  }
}

// hide navbar button
[aria-controls="menu-list"] {
  justify-self: flex-end;
  display: none;
  background: $black;
  color: $white;
  width: 5rem;
  height: 5rem;
  font-size: 2rem;
  // margin-bottom: 1rem;
  border: none;
  border-radius: .5rem;
}

@media (max-width: 1250px) {
  .nav__section {
    width: 90%;
  }
}

@media (max-width: 900px) {
  .nav__section {
    width: 100%;
  }
}

@media (max-width: 800px) {
  .nav {
    // hide the navbar
    height: 0;
    overflow: hidden;
    &__item:hover > &__dropdown {
      display: none;
    }
    &__link {
      justify-content: space-between;
    }
    &__title {
      padding: 0 1rem;
    }
    &__dropdown {
      position: relative;
      &__link {
        width: 100%;
        font-size: 1.5rem;
        height: 4rem;
        span {
          padding: 0 2rem;
        }
      }
    }
  }

  .nav__section {
    &--pages {
      grid-template-columns: 1fr;
    }
  }

  // display navbar button
  [aria-controls="menu-list"] {
    display: block;
  }

  [aria-expanded="false"] > .close {
    display: none;
  }

  [aria-expanded="true"] > .close {
    display: block;
  }

  [aria-expanded="true"] > .open {
    display: none;
  }

  // display navbar
  [aria-expanded="true"] ~ .nav {
    height: 100%;
    .down {
      padding: 0 1rem;
    }
  }

  // toggle navbar dropdown
  [aria-expanded="false"] ~.nav__dropdown {
    display: none !important;
  }
  [aria-expanded="false"] > .down .fa-angle-up {
    display: none;
  }
  [aria-expanded="false"] > .down .fa-angle-down {
    display: block;
  }

  [aria-expanded="true"] ~.nav__dropdown {
    display: block !important;
  }
  [aria-expanded="true"] > .down .fa-angle-up {
    display: block;
  }
  [aria-expanded="true"] > .down .fa-angle-down {
    display: none;
  }
}



